export default function HorizontalLabelPositionBelowStepper() {
    const classes = useStyles();
    const [activeStep, setActiveStep] = React.useState(0);
    const [checked, setChecked] = useState(
        {
            agree01 : false,
            agree02 : false,
        }
    );
    const steps = getSteps();

    const handleNext = () => {
        if((checked.agree01 && checked.agree02)) {
            setActiveStep((prevActiveStep) => prevActiveStep + 1);
        }else {
            alert(".");
        }
    };

    const handleBack = () => {
        setActiveStep((prevActiveStep) => prevActiveStep - 1);
    };

    const headerstyle =  {
        textAlign : "center",
    }

    function getStepContent(stepIndex) {

        switch (stepIndex) {
            case 0:
            
            return (
                <>
                    <Terms onCheck={(checkedTarget) => {
                        // console.dir(checkedTarget.target);
                        // console.dir(checkedTarget);
                        setChecked({
                            ...checked,
                            //  agree01 , agree02  []  <- 
                            // []  String 
                            [checkedTarget.target.id] : checkedTarget.target.checked,
                        })
                        // console.log([checkedTarget.target.id]);
                        // console.log([checkedTarget.target.checked]);
                    }}/>
                </>
            );
            case 1:
            return (
                <>
                    <InfoInput />
                </>
            );
            case 2:
            return(
                <>
                    <SignUpComp />
                </>
            );
            // default:
            // return 'Unknown stepIndex';
        }
    }
    console.log(checked);
    return (
    <div className="steper">
        {/* <Navbar /> */}
        <h1 style={headerstyle}></h1>
        <div className={classes.root}>
        <Stepper activeStep={activeStep} alternativeLabel={false}>
            {steps.map((label) => (
            <Step key={label}>
                <StepLabel>{label}</StepLabel>
            </Step>
            ))}
        </Stepper>
        <div>
            {activeStep === steps.length ? (
            <div>
                <Typography className={classes.instructions}>All steps completed</Typography>
                {/* <Button onClick={handleReset}>Reset</Button> */}
            </div>
            ) : (
            <div>
                {getStepContent(activeStep)}
                <div>
                <Button
                    disabled={activeStep === 0}
                    onClick={handleBack}
                    className={classes.backButton}
                >
                {activeStep === 0 || activeStep === 1 ? "" : <Link to="/" className={classes.mainButton}>{""}</Link>}
                </Button>
                <Button variant="contained" color="primary" onClick={handleNext}>
                    {/*   Link    */}
                    {activeStep === steps.length - 1 ? <Link to ="/LogIn" className={classes.LogInButton}>{''}</Link> : ''}
                </Button>
                </div>
            </div>
            )}
        </div>
        </div>
    </div>
    );
}